.columns {
	margin-left: -$column-gap;
	margin-right: -$column-gap;
	margin-top: -$column-gap;

	&:last-child {
		margin-bottom: -$column-gap;
	}

	&:not(:last-child) {
		margin-bottom: calc(15px - #{$column-gap});
	}

	// Modifiers
	&.is-centered {
		justify-content: center;
	}

	&.is-gapless {
		margin-left: 0;
		margin-right: 0;
		margin-top: 0;

		& > .column {
			margin: 0;
			padding: 0 !important;
		}

		&:not(:last-child) {
			margin-bottom: 15px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	&.is-mobile {
		display: flex;
	}

	&.is-multiline {
		flex-wrap: wrap;
	}

	&.is-vcentered {
		align-items: center;
	}

	// Responsiveness
	@include tablet {
		&:not(.is-desktop) {
			display: flex;
		}
	}

	@include desktop {
		// Modifiers
		&.is-desktop {
			display: flex;
		}
	}
}

@if $variable-columns {
	.columns.is-variable {
		--columnGap: 8px;
		margin-left: calc(-1 * var(--columnGap));
		margin-right: calc(-1 * var(--columnGap));

		.column {
			padding-left: var(--columnGap);
			padding-right: var(--columnGap);
		}

		@for $i from 0 through 8 {
			&.is-#{$i} {
				--columnGap: #{$i * 3px};
			}

			@include mobile {
				&.is-#{$i}-mobile {
					--columnGap: #{$i * 3px};
				}
			}

			@include tablet {
				&.is-#{$i}-tablet {
					--columnGap: #{$i * 3px};
				}
			}

			@include tablet-only {
				&.is-#{$i}-tablet-only {
					--columnGap: #{$i * 3px};
				}
			}

			@include touch {
				&.is-#{$i}-touch {
					--columnGap: #{$i * 3px};
				}
			}

			@include desktop {
				&.is-#{$i}-desktop {
					--columnGap: #{$i * 3px};
				}
			}

			@include desktop-only {
				&.is-#{$i}-desktop-only {
					--columnGap: #{$i * 3px};
				}
			}

			@include widescreen {
				&.is-#{$i}-widescreen {
					--columnGap: #{$i * 3px};
				}
			}

			@include widescreen-only {
				&.is-#{$i}-widescreen-only {
					--columnGap: #{$i * 3px};
				}
			}

			@include fullhd {
				&.is-#{$i}-fullhd {
					--columnGap: #{$i * 3px};
				}
			}
		}
	}
}
