$label-color: $text-strong !default;
$label-weight: $weight-bold !default;

$help-size: $size-small !default;

.label {
	color: $label-color;
	display: block;
	font-size: $size-4;
	// font-weight: $label-weight;

	&:not(:last-child) {
		margin-bottom: 0.5em;
	}

	// Sizes
	&.is-small {
		font-size: $size-small;
	}

	&.is-medium {
		font-size: $size-medium;
	}

	&.is-large {
		font-size: $size-large;
	}
}

.help {
	display: block;
	font-size: $help-size;
	margin-top: 2.5px;

	@each $name, $pair in $colors {
		$color: nth($pair, 1);

		&.is-#{$name} {
			color: $color;
		}
	}
}

// Containers
.row {
	&:not(:last-child) {
		margin-bottom: 2rem;
	}
}

.field {
	&:not(:last-child) {
		margin-bottom: 1rem;
	}

	// Modifiers
	&.has-addons {
		display: flex;
		justify-content: flex-start;

		.control {
			&:not(:last-child) {
				margin-right: -1px;
			}

			&:not(:first-child):not(:last-child) {
				.button,
				.input,
				.select select {
					border-radius: 0;
				}
			}

			&:first-child:not(:only-child) {
				.button,
				.input,
				.select select {
					border-bottom-right-radius: 0;
					border-top-right-radius: 0;
				}
			}

			&:last-child:not(:only-child) {
				.button,
				.input,
				.select select {
					border-bottom-left-radius: 0;
					border-top-left-radius: 0;
				}
			}

			.button,
			.input,
			.select select {
				&:not([disabled]) {
					&:hover,
					&.is-hovered {
						z-index: 2;
					}

					&:focus,
					&.is-focused,
					&:active,
					&.is-active {
						z-index: 3;

						&:hover {
							z-index: 4;
						}
					}
				}
			}

			&.is-expanded {
				flex-grow: 1;
				flex-shrink: 1;
			}
		}

		&.has-addons-centered {
			justify-content: center;
		}

		&.has-addons-right {
			justify-content: flex-end;
		}

		&.has-addons-fullwidth {
			.control {
				flex-grow: 1;
				flex-shrink: 0;
			}
		}
	}

	&.is-grouped {
		display: flex;
		justify-content: flex-start;

		& > .control {
			flex-shrink: 0;

			&:not(:last-child) {
				margin-bottom: 0;
				margin-right: 10px;
			}

			&.is-expanded {
				flex-grow: 1;
				flex-shrink: 1;
			}

			&.select-tel {
				width: 8rem;
			}
		}

		&.is-grouped-centered {
			justify-content: center;
		}

		&.is-grouped-right {
			justify-content: flex-end;
		}

		&.is-grouped-multiline {
			flex-wrap: wrap;

			& > .control {
				&:last-child,
				&:not(:last-child) {
					margin-bottom: 8px;
				}
			}

			&:last-child {
				margin-bottom: -8px;
			}

			&:not(:last-child) {
				margin-bottom: 0;
			}
		}
	}

	&.is-horizontal {
		@include tablet {
			display: flex;
		}
	}
}

.field-label {
	.label {
		font-size: inherit;
	}

	@include mobile {
		margin-bottom: 50px;
	}

	@include tablet {
		flex-basis: 0;
		flex-grow: 1;
		flex-shrink: 0;
		margin-right: 15px;
		text-align: right;

		&.is-small {
			font-size: $size-small;
			padding-top: 0.375em;
		}

		&.is-normal {
			padding-top: 0.375em;
		}

		&.is-medium {
			font-size: $size-medium;
			padding-top: 0.375em;
		}

		&.is-large {
			font-size: $size-large;
			padding-top: 0.375em;
		}
	}
}

.field-body {
	.field .field {
		margin-bottom: 0;
	}

	@include tablet {
		display: flex;
		flex-basis: 0;
		flex-grow: 5;
		flex-shrink: 1;

		.field {
			margin-bottom: 0;
		}

		& > .field {
			flex-shrink: 1;

			&:not(.is-narrow) {
				flex-grow: 1;
			}

			&:not(:last-child) {
				margin-right: 8px;
			}
		}
	}
}

.control {
	box-sizing: border-box;
	clear: both;
	font-size: $size-normal;
	position: relative;
	text-align: left;

	// Modifiers
	&.has-icons-left,
	&.has-icons-right {
		.input,
		.select {
			&:focus {
				& ~ .icon {
					color: $input-icon-active-color;
				}
			}

			&.is-small ~ .icon {
				font-size: $size-small;
			}

			&.is-medium ~ .icon {
				font-size: $size-medium;
			}

			&.is-large ~ .icon {
				font-size: $size-large;
			}
		}

		.icon {
			color: $input-icon-color;
			height: $input-height;
			pointer-events: none;
			position: absolute;
			top: 0;
			width: $input-height;
			z-index: 4;
		}
	}

	&.has-icons-left {
		.input,
		.select select {
			padding-left: $input-height;
		}

		.icon.is-left {
			left: 0;
		}
	}

	&.has-icons-right {
		.input,
		.select select {
			padding-right: $input-height;
		}

		.icon.is-right {
			right: 0;
		}
	}

	&.is-loading {
		&::after {
			@extend %loader;

			position: absolute !important;
			right: 0.625em;
			top: 0.625em;
			z-index: 4;
		}

		&.is-small:after {
			font-size: $size-small;
		}

		&.is-medium:after {
			font-size: $size-medium;
		}

		&.is-large:after {
			font-size: $size-large;
		}
	}
}
