@import 'iconset_variables';

@font-face {
	font-family: '#{$icomoon-font-family}';
	src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?idp2xy');
	src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?idp2xy#iefix') format('embedded-opentype'), url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?idp2xy') format('truetype'),
		url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?idp2xy') format('woff'), url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?idp2xy##{$icomoon-font-family}') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^='ico-'],
[class*=' ico-'] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: '#{$icomoon-font-family}' !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

