// 대부분 모바일 기기 대응
@mixin mobile-device {
	@media screen and (max-width: 768px) {
		@content;
	}
}

@mixin mobile-640 {
	@media screen and (max-width: $mobile-640) {
		@content;
	}
}

@mixin mobile-480 {
	@media screen and (max-width: $mobile-480) {
		@content;
	}
}
@mixin mobile-420 {
	@media screen and (max-width: $mobile-420) {
		@content;
	}
}
@mixin mobile-360 {
	@media screen and (max-width: $mobile-360) {
		@content;
	}
}
@mixin mobile-330 {
	@media screen and (max-width: $mobile-330) {
		@content;
	}
}
// mobile - 390
@mixin mobile {
	@media screen and (max-width: $mobile) {
		@content;
	}
}

@mixin tablet {
	@media screen and (min-width: $tablet), print {
		@content;
	}
}

@mixin tablet-only {
	@media screen and (min-width: $tablet) and (max-width: $desktop - 1px) {
		@content;
	}
}

@mixin touch {
	@media screen and (max-width: $desktop - 1px) {
		@content;
	}
}

@mixin desktop {
	@media screen and (min-width: $desktop) {
		@content;
	}
}

@mixin desktop-only {
	@if $widescreen-enabled {
		@media screen and (min-width: $desktop) and (max-width: $widescreen - 1px) {
			@content;
		}
	}
}

@mixin until-widescreen {
	@if $widescreen-enabled {
		@media screen and (max-width: $widescreen - 1px) {
			@content;
		}
	}
}

@mixin widescreen {
	@if $widescreen-enabled {
		@media screen and (min-width: $widescreen) {
			@content;
		}
	}
}

@mixin widescreen-only {
	@if $widescreen-enabled and $fullhd-enabled {
		@media screen and (min-width: $widescreen) and (max-width: $fullhd - 1px) {
			@content;
		}
	}
}

@mixin until-fullhd {
	@if $fullhd-enabled {
		@media screen and (max-width: $fullhd - 1px) {
			@content;
		}
	}
}

@mixin fullhd {
	@if $fullhd-enabled {
		@media screen and (min-width: $fullhd) {
			@content;
		}
	}
}

// Placeholders

@mixin unselectable {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

%unselectable {
	@include unselectable;
}

@mixin arrow($color: transparent) {
	border: 1.5px solid $color;
	border-radius: 1.5px;
	border-right: 0;
	border-top: 0;
	content: ' ';
	display: block;
	width: 0.75em;
	height: 0.75em;
	margin-top: -0.6em;
	pointer-events: none;
	position: absolute;
	top: 50%;
	transform: rotate(-45deg);
	transform-origin: center;
}

%arrow {
	@include arrow;
}

@mixin block($spacing: $block-spacing) {
	&:not(:last-child) {
		margin-bottom: $spacing;
	}
}

%block {
	@include block;
}

@mixin delete {
	@extend %unselectable;

	-moz-appearance: none;
	-webkit-appearance: none;
	background-color: bulmaRgba($scheme-invert, 0.2);
	border: none;
	border-radius: $radius-rounded;
	cursor: pointer;
	pointer-events: auto;
	display: inline-block;
	flex-grow: 0;
	flex-shrink: 0;
	font-size: 0;
	height: 20px;
	max-height: 20px;
	max-width: 20px;
	min-height: 20px;
	min-width: 20px;
	outline: none;
	position: relative;
	vertical-align: top;
	width: 20px;

	&::before,
	&::after {
		background-color: $scheme-main;
		content: '';
		display: block;
		left: 50%;
		position: absolute;
		top: 50%;
		transform: translateX(-50%) translateY(-50%) rotate(45deg);
		transform-origin: center center;
	}

	&::before {
		height: 2px;
		width: 50%;
	}

	&::after {
		height: 50%;
		width: 2px;
	}

	&:hover,
	&:focus {
		background-color: bulmaRgba($scheme-invert, 0.3);
	}

	&:active {
		background-color: bulmaRgba($scheme-invert, 0.4);
	}

	// Sizes
	&.is-small {
		height: 16px;
		max-height: 16px;
		max-width: 16px;
		min-height: 16px;
		min-width: 16px;
		width: 16px;
	}

	&.is-medium {
		height: 24px;
		max-height: 24px;
		max-width: 24px;
		min-height: 24px;
		min-width: 24px;
		width: 24px;
	}

	&.is-large {
		height: 32px;
		max-height: 32px;
		max-width: 32px;
		min-height: 32px;
		min-width: 32px;
		width: 32px;
	}
}

%delete {
	@include delete;
}

@mixin loader {
	animation: spinAround 500ms infinite linear;
	border: 2px solid $grey-lighter;
	border-radius: $radius-rounded;
	border-right-color: transparent;
	border-top-color: transparent;
	content: '';
	display: block;
	height: 1em;
	position: relative;
	width: 1em;
}

%loader {
	@include loader;
}

@mixin overlay($offset: 0) {
	bottom: $offset;
	left: $offset;
	position: absolute;
	right: $offset;
	top: $offset;
}

%overlay {
	@include overlay;
}

// 추가
@mixin text-ellipsis {
	display: block;
	word-wrap: normal;
	word-break: break-all;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}
